*{
 margin: 0;
 padding: 0;
 box-sizing: border-box;
}

.container{
  display: flex; 
 align-items: center;
margin: auto;
 /* flex-direction: column; */
 color: white;
 width: 70%;
 background-color: rgb(0, 0, 0); 
 border-radius: 10px;
 margin: auto;
}

.row{
 display: flex;
 justify-content:space-evenly;
 /* justify-self: auto; */
 text-align: center;
 align-items: center;
 /* height: 70px; */
 width:60%; 
 flex-wrap: wrap;
 padding: 0px 20px;
 
}

.coins{
  display: flex;
  margin: auto 20px;
  padding: 18px;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  font-size: 10px;
max-width: 300px;
}

.coins img{
  margin: 0px 15px ;
 height: 40px;
 width: 40px;
}

.name{
  width: 80px;
  text-align: center;
}



.symbol{
 text-transform: uppercase;
}


 .price{
   text-align: center;
  /* width: 100px; */
 
 }

@media only screen and (max-width: 630px  ) {
    .coins{
      background-color: rgba(7, 6, 90, 0.836);
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-items:center;
      margin: 10px;
      padding: 8px;
      font-size:10px;
      color: rgb(255, 255, 255);
      width:45%;
      /* flex-wrap: wrap; */
    }
  
  .row {
    width: 50%;
    align-items: start;
    flex-direction: column;
      
    }
  .container{
    width: 85%;
  }
  .coin-input{
    width: 70%;
  }
  }

 .red{
  color: red;
 }

 .green{
  color: green;
 }

 .line{
  
  background-color: rgb(255, 255, 255);
  height: 2px;
  outline: none;
  width: 60%;
  margin: auto;
 }